<template>
  <div>
    <!-- 按钮切换图表类型 -->
    <button @click="toggleChartType">切换</button>
    
    <!-- ECharts 容器 -->
    <div ref="echarts" style="width: 100%; height: 400px;"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';

export default {
  name: 'MyEChart',
  data() {
    return {
      chartData: [5, 20, 36, 10, 10, 20, 40], // 图表数据
      chartType: 'line', // 控制当前显示的是折线图（line）还是柱状图（bar）
    };
  },
  mounted() {
    this.initChart();
  },
  watch: {
    // 监听 chartData 变化，更新图表数据
    chartData(newData) {
      if (this.chart) {
        this.chart.setOption({
          series: [
            {
              data: newData,
            },
          ],
        });
      }
    },
    // 监听 chartType 变化，更新图表类型
    chartType(newType) {
      if (this.chart) {
        this.updateChartType(newType);
      }
    },
  },
  methods: {
    // 初始化图表
    initChart() {
      const chartContainer = this.$refs.echarts;
      this.chart = echarts.init(chartContainer);

      // 设置初始图表配置
      const option = this.getChartOption(this.chartType, this.chartData);
      this.chart.setOption(option);
    },

    // 获取图表配置
    getChartOption(type, data) {
      return {
        title: {
          text: type === 'line' ? '折线图示例' : '柱状图示例',
        },
        tooltip: {
          trigger: 'axis',
        },
        xAxis: {
          type: 'category',
          data: ['一月', '二月', '三月', '四月', '五月', '六月', '七月'],
        },
        yAxis: {
          type: 'value',
        },
        series: [
          {
            name: '销量',
            type: type, // 根据 chartType 动态设置图表类型
            data: data,
          },
        ],
      };
    },

    // 更新图表类型
    updateChartType(type) {
      const option = this.getChartOption(type, this.chartData);
      this.chart.setOption(option);
    },

    // 切换图表类型
    toggleChartType() {
      // 切换图表类型
      this.chartType = this.chartType === 'line' ? 'bar' : 'line';
    },
  },
};
</script>

<style scoped>
/* 可根据需要添加样式 */
</style>
